import "./css/Home.css";
import { useContext, useEffect } from "react";
import AlertContext from "../context/alert/AlertContext";
import LoginContext from "../context/login/LoginContext";
import ProductContext from "../context/product/ProductContext";

import Slider from "./Slider";  

import ProductSlider from "./ProductSlider";

const Home = () => {
  const a = useContext(AlertContext);
  const l = useContext(LoginContext);
  const pr = useContext(ProductContext);


  useEffect(() => {
    l.checkUser();
    // eslint-disable-next-line
  }, [l.authToken])

  return (
    <>
      <main className="homeMainCon">
        {a.showAlert()}

        <Slider className="sliderCon"/>
        <h1> </h1>
        <ProductSlider title="" items={pr.products.filter(product=>product.category==="")}/>
        <ProductSlider title="" items={pr.products.filter(product=>product.category==="")}/>
        <ProductSlider title="" items={pr.products.filter(product=>product.category==="")}/>
        <h1>  </h1>
        <ProductSlider title="" items={pr.products.filter(product=>product.category==="")}/>
        <ProductSlider title="" items={pr.products.filter(product=>product.category==="")}/>
        <ProductSlider title="" items={pr.products.filter(product=>product.category==="")}/>

        
      </main>
    </>
  );
}
export default Home;